export function getMissions(lordCard, decks) {
    return MISSIONS.map(toMission);
    function toMission({description, playerVictoryPoints, bossVictoryPoints, bossLord, bossDeck, board}, idx) {
        return {
            description,
            mission: {
                players: [
                    {
                        targetVictoryPoints: playerVictoryPoints,
                        lordCard,
                        type: "alphabeta",
                        depth: 1,
                        deck:   decks[idx],
                        actionPruneLevel: 0,
                        qSearchDepth: 2
                    },
                    {
                        targetVictoryPoints: bossVictoryPoints,
                        lordCard: bossLord,
                        type: "alphabeta",
                        depth: 1,
                        deck: bossDeck,
                        actionPruneLevel: 0,
                        qSearchDepth: 2
                    }
                ],
                board
            }
        }
    }
}


